.App {
  background-color: #3a3939;
  padding-top: 10px;
  width: 100%;
  height: 100vh;

  display: flex;
}
body {
  overflow: hidden;
}
.invoiceViewer {
  overflow: auto;
  height: 90vh;
  width: 30cm;
  /* padding: 2cm; */
  padding-bottom: 50px;
  margin: auto;
}
.container {
  width: 21cm;
  height: auto;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 2cm;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  zoom: 100%;
}

.content {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  /* border: 1px solid #ccc; */
  border-radius: 5px;
  padding: 20px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}
table,
th,
td {
  border: 0px;
}
.phoneCall {
  font-size: 18px;
  font-weight: bold;
}
.emailTo > a {
  color: #094cb4;
  font-size: 18px;
  font-weight: bold;
}
.phoneCall0 {
  color: #094cb4;
}
.signatureContentBoldElement {
  font-weight: bold;
}
.pElementMarginBlockStartAndEnd {
  height: 80px;
  width: 100%;
  /* border: 1px solid; */
}

.ButtonStylePrint {
  width: 80px;
  height: 35px;
  border-radius: 3px;

  border: none;
  background-color: #3a3939;
  color: white;
  /* position: absolute; */
  top: 60px;
  left: 0;
  right: 0;
  font-size: larger;
  margin: 10px auto;
}
.SideBar {
  width: 100px;
  height: 80vh;
  margin: auto 0px;
  background-color: white;
  text-align: center;
}
